}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
/* Сделать примесь */
ul {
  list-style: none;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.button {
  padding: 20px 30px 17px 33px;
  font-size: 18px;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  color: #ffffff;
  border: solid 3px #ffffff;
  border-radius: 30px;
}
.button-download:hover {
  box-shadow: 0 5px 7px 0 #010101c0;
  border: solid 3px #ffffff;
  background-color: #ffffff;
  color: #d22856;
}
.button-download:active {
  color: rgba(210, 40, 86, 0.3);
}
.button--pink {
  color: #d22856;
  border: solid 3px #d22856;
}
.button--pink:hover {
  background-color: #d22856;
  color: #ffffff;
  border: solid 3px #d22856;
}
.button--pink:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--cancel {
  opacity: 0.1;
  border: solid 3px #283645;
  color: #283645;
}
.button--cancel:hover {
  opacity: 0.3;
  border: solid 3px #283645;
  color: #283645;
}
.button--cancel:active {
  opacity: 0.3;
  border: solid 3px #283645;
  color: #ffffff;
  background-color: #283645;
}
.button--submit {
  padding-top: 14px;
  border: solid 2px #2aab6d;
  background-color: #2aab6d;
  font-size: 18px;
  font-weight: bold;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 0%;
  width: 100%;
}
.button--submit:hover {
  border: solid 2px #1c9b5e;
  background-color: #1c9b5e;
}
.button--submit:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--submit:disabled {
  border: solid 2px #e5e5e5;
  background-color: #e5e5e5;
}
@media (min-width: 660px) {
  .button--submit {
    padding: 14px 55px 15px 55px;
    margin: 40px 0;
    width: auto;
    min-width: 295px;
  }
  .popup .button--submit {
    margin: 0;
  }
}
@media (min-width: 1200px) {
