.main-nav {
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav--opened {
  min-height: 66px;
  background-color: #283645;
}
.main-nav__list {
  margin: 0;
  text-align: center;
}
@media (max-width: 1199px) {
  .main-nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 64px;
    height: 50px;
    background-color: transparent;
    cursor: pointer;
    border: 0;
    outline: 0;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    z-index: 3;
    position: absolute;
    top: 15;
    left: 0;
    width: 50px;
    height: 3px;
    background: #ffffff;
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  }
  .main-nav--closed .main-nav__toggle:hover::before {
    background-color: #d22856;
    box-shadow: 0 8px 0 0 #d22856, 0 16px 0 0 #d22856;
  }
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: rgba(210, 40, 86, 0.3);
    box-shadow: 0 8px 0 0 rgba(210, 40, 86, 0.3), 0 16px 0 0 rgba(210, 40, 86, 0.3);
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 31px;
    right: 15px;
    width: 30px;
    height: 4px;
    background: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:hover::before,
  .main-nav--opened .main-nav__toggle:hover::after {
    background-color: #d22856;
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    background: rgba(210, 40, 86, 0.3);
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 22px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
